import React, { useState, useRef, useEffect } from 'react';
import '../styles/ChatArea.css';

function AgentsChat() {
  const [messages, setMessages] = useState([]);
  const [inputMessage, setInputMessage] = useState('');
  const [selectedAgent, setSelectedAgent] = useState('Agent 1');
  const messagesEndRef = useRef(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(scrollToBottom, [messages]);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (inputMessage.trim() !== '') {
      setMessages([...messages, { text: inputMessage, sender: 'user' }]);
      setInputMessage('');
      // Simulate agent response
      setTimeout(() => {
        setMessages(prevMessages => [...prevMessages, { text: `This is a simulated response from ${selectedAgent} in the Agents Chat.`, sender: 'agent' }]);
      }, 1000);
    }
  };

  return (
    <div className="chat-container">
      <div className="agent-selector">
        <label htmlFor="agent-select">Select an agent: </label>
        <select 
          id="agent-select"
          value={selectedAgent} 
          onChange={(e) => setSelectedAgent(e.target.value)}
        >
          <option value="Agent 1">Agent 1</option>
          <option value="Agent 2">Agent 2</option>
          <option value="Agent 3">Agent 3</option>
          <option value="Agent 4">Agent 4</option>
        </select>
      </div>
      <div className="messages-container">
        {messages.map((message, index) => (
          <div key={index} className={`message ${message.sender}`}>
            {message.text}
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>
      <form onSubmit={handleSubmit} className="input-form">
        <input
          type="text"
          value={inputMessage}
          onChange={(e) => setInputMessage(e.target.value)}
          placeholder={`Type your message for ${selectedAgent}...`}
          className="message-input"
        />
        <button type="submit" className="send-button">Send</button>
      </form>
    </div>
  );
}

export default AgentsChat;