import React, { useState } from 'react';
import FileAttachment from './FileAttachment';
import '../styles/KnowledgeBase.css';

function KnowledgeBase() {
  const [files, setFiles] = useState([
    { id: 1, name: 'filled_form.pdf', type: 'pdf' },
    { id: 2, name: '1st.png', type: 'image' },
    { id: 3, name: 'php.png', type: 'image' },
    { id: 4, name: 'css-3.png', type: 'image' },
    { id: 5, name: 'html.png', type: 'image' },
    { id: 6, name: 'java-script.png', type: 'image' },
    { id: 7, name: 'python.png', type: 'image' },
    { id: 8, name: 'png-file.png', type: 'image' },
    { id: 9, name: 'doc.png', type: 'image' },
    { id: 10, name: 'xls.png', type: 'image' },
    { id: 11, name: 'pdf.png', type: 'image' }
  ]);

  const handleFileAttach = (newFiles) => {
    const newFileObjects = newFiles.map((file, index) => ({
      id: files.length + index + 1,
      name: file.name,
      type: file.type.split('/')[0]
    }));
    setFiles([...files, ...newFileObjects]);
  };

  return (
    <div className="knowledge-base">
      <h2>Database</h2>
      <FileAttachment onFileAttach={handleFileAttach} />
      <ul className="file-list">
        {files.map((file) => (
          <li key={file.id} className="file-item">
            <img src={`/icons/${file.type}-icon.png`} alt={file.type} className="file-icon" />
            <span className="file-name">{file.name}</span>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default KnowledgeBase;